html {
  box-sizing: border-box;
  height: 100%; }

body {
  min-width: 320px;
  height: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  background-color: #ffffff; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed Regular"), url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed Bold"), url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: local("Roboto Medium"), url("../fonts/roboto-v20-latin_cyrillic-500.woff2") format("woff2"), url("../fonts/roboto-v20-latin_cyrillic-500.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: local("PT Mono"), url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff") format("woff"); }

.page-header {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1461px;
  margin-right: auto;
  margin-left: auto;
  padding: 33px 25px 0;
  background-color: black; }

.page-header__logo {
  flex: 0 0;
  width: 202px;
  height: 52px;
  margin-right: 8.363%; }

.page-header__logo a::before {
  content: url("../img/header-logo_desktop.svg");
  width: 202px;
  height: 52px; }

.nav {
  display: flex;
  flex: 1 0;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center; }

.nav__burger-button {
  display: block;
  flex: 1 0 37px;
  width: 37px;
  height: 13px;
  margin-right: 20px;
  background-color: transparent;
  background-image: url("../img/icon-two_bars.svg");
  background-repeat: no-repeat;
  background-size: 37px auto;
  border: none;
  cursor: pointer; }
  .nav__burger-button:hover {
    opacity: 0.7; }
  .nav__burger-button:active {
    opacity: 0.3; }

.nav a {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff; }
  .nav a:hover {
    opacity: 0.7; }
  .nav a:active {
    opacity: 0.3; }

.nav__email {
  margin-right: 8.066%; }

.nav__email a {
  display: flex;
  align-items: center; }
  .nav__email a::before {
    content: "";
    display: block;
    width: 19px;
    height: 14px;
    margin-right: 20px;
    background-image: url("../img/icon-mail.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 19px 14px; }

.nav__phone {
  margin-right: 4.583%; }

.nav__phone a {
  display: flex;
  align-items: center; }
  .nav__phone a::before {
    content: "";
    display: block;
    width: 18px;
    height: 18px;
    margin-right: 16px;
    background-image: url("../img/icon-phone.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 18px 18px; }

.nav__callback-button {
  display: block;
  box-sizing: border-box;
  padding: 13.5px 26.5px;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  .nav__callback-button:hover {
    color: #00b2ff;
    background-color: #ffffff;
    border: 1px solid #00b2ff; }
  .nav__callback-button:active {
    opacity: 0.3; }

@media (max-width: 1023px) {
  .page-header {
    padding: 20px 25px 0; }
  .nav {
    padding: 0 1px 5px; }
  .nav__burger-button {
    flex: 0 0 auto;
    margin-right: 37px; }
  .nav a {
    font-size: 0; }
  .nav__email {
    margin-right: 33px; }
  .nav__email a::before {
    width: 25px;
    height: 24px;
    margin-right: 0;
    background-size: 25px 19px; }
  .nav__phone {
    margin-right: 0; }
  .nav__phone a::before {
    width: 24px;
    height: 24px;
    margin-right: 0;
    background-size: 24px 24px; }
  .nav__callback-button {
    display: none; } }

@media (max-width: 767px) {
  .page-header {
    padding: 15px 17px 0; }
  .nav {
    padding: 0 0 15px; }
  .page-header__logo a::before {
    content: url("../img/header-logo_mobile.svg");
    width: 38px;
    height: 38px; }
  .nav__email {
    margin-right: 22px; }
  .nav__burger-button {
    margin-right: 27px; } }
